.page__content {
  width: 320px;
  margin: auto;
  max-width: 100%;
  overflow: hidden;
  margin: 0 auto;
  background-color: $background-main;
}

@media (min-width: $tablet-width) {
  .page__content {
    width: 768px;
  }
}

@media (min-width: 1200px) {
  .page__content {
    width: 1200px;
    box-shadow: #E7E5E5;
    margin: 0 auto;
  }
}
